import { BrowserModule }        from '@angular/platform-browser';
import { ErrorHandler,
         NgModule }             from '@angular/core';
import { IonicApp,
         IonicErrorHandler,
         IonicModule }          from 'ionic-angular';

import { App }                  from './app.component';
import { HomePage }             from '../pages/home/home';
import { ListPage }             from '../pages/list/list';

import { StatusBar }            from '@ionic-native/status-bar';
import { SplashScreen }         from '@ionic-native/splash-screen';

@NgModule({
    declarations: [
        App,
        HomePage,
        ListPage
    ],
    imports: [
        BrowserModule,
        IonicModule.forRoot(App, {}, 


{
  links: [
    { loadChildren: '../pages/quadros/quadros.module#QuadrosComponentModule', name: 'quadros', segment: 'quadros', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/quadro-detalhe/quadro-detalhe.module#QuadroDetalheComponentModule', name: 'quadro/show', segment: 'quadro/:id', priority: 'low', defaultHistory: [] }
  ]
}),
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        App,
        HomePage,
        ListPage
    ],
    providers: [
        StatusBar,
        SplashScreen,
        {
            provide: ErrorHandler, useClass: IonicErrorHandler
        }
    ]
})
export class AppModule {}



